import React, { useEffect, useState } from "react";
import Layout from "../../layout/layout";
import { useAppContext } from "../../contexts/AppContext";
import { validate } from "react-email-validator";

import "./login.scss";
import Input from "../../components/input";
import { PrimaryButton } from "../../components/button";
import { Link, useNavigate } from "react-router-dom";
const LoginPage = (props) => {
  const context = useAppContext();
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [valid, setValid] = useState(false);

  useEffect(() => {
    setValid(validate(email) && password && password.length > 6);
  }, [email, password]);

  const handleLogin = () => {
    if (context && validate(email)) {
      context.setUser({ email: email, name: "Sasuke Uchia" });
    }
    navigate("/");
  };
  return (
    <Layout title={"Login"} page="login">
      <div className="login-container min-h-screen px-4">
        <div className="container mx-auto pt-28">
          <div className="rounded-2xl bg-white p-8 sm:p-12 space-y-6 md:w-2/3 lg:w-1/2 xl:w-1/3 mx-auto">
            <h1 className="text-4xl font-bold">Account Login</h1>
            <p>Log in to your account to check your policy and manage claims</p>
            <Input
              label={"Email"}
              value={email}
              setValue={setEmail}
              type="email"
              valid={validate(email)}
            />
            <Input
              label={"Password"}
              value={password}
              setValue={setPassword}
              type="password"
              valid={password.length > 6}
            />
            <PrimaryButton
              className="w-full text-xl py-3 px-4 h-14"
              action={handleLogin}
              active={valid}
            >
              Log in
            </PrimaryButton>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default LoginPage;
