const Input = (props) => {
  const { className, value, setValue, type, label, valid } = props;

  return (
    <div>
      <p className="py-2">{label}</p>
      <input
        value={value}
        onChange={(e) => setValue(e.target.value)}
        className={`${className} w-full outline-none focus:ring-2 ${valid?"ring-slate-200 focus:border-slate-800 bg-transparent":"ring-red-200 focus:border-red-500 bg-red-50"}  border border-slate-8200  rounded-md p-2`}
        type={type}
      />
    </div>
  );
};

export default Input;
