import React, { useState, useEffect } from "react";
import { Calendar } from "react-date-range";
import { useParams } from "react-router-dom";
import Swal from "sweetalert2";
import Moment from 'moment';
import "react-date-range/dist/styles.css"; // main css file
import "react-date-range/dist/theme/default.css"; // theme css file
import Layout from "../../../layout/layout";
import { doctors } from "../../../utils/api";
import { ItemTime } from "../../../components/item";
import { PrimaryButton } from "../../../components/button";
import axios from "axios";

const ProfilePage = (props) => {
  const params = useParams();
  const doctor = doctors.find((e) => e.id === params.doctorId);
  const [date, setDate] = useState(null);
  const [validTime, setValidTime] = useState(null);
  const [selectedTime, setSelectedTime] = useState(null);
  const [isToday, setIsToday] = useState(false);
  
  const bookHandler = () => {
    Swal.fire({
      title: "Submit your Name",
      input: "text",
      inputAttributes: {
        autocapitalize: "off",
      },
      showCancelButton: true,
      confirmButtonText: "Book",
      showLoaderOnConfirm: true,
      preConfirm: (name) => {
        return axios({
          url: "https://fe-assignment-api.herokuapp.com/booking",
          method: "post",
          headers: {
            "x-api-key": process.env.REACT_APP_NECKTIE_APIKEY,
            "Content-Type": "application/json",
          },
          params: {
            name: name,
            doctorId: doctor.id,
            start: selectedTime,
            date: Moment(date).format("YYYY-MM-DD"),
          },
        })
          .then((response) => {
            if (!response.ok) {
              throw new Error(response.statusText);
            }
            return response.json();
          })
          .catch((error) => {
            Swal.showValidationMessage(`Request failed: ${error}`);
          });
      },
      allowOutsideClick: () => !Swal.isLoading(),
    }).then((result) => {
      if (result.isConfirmed) {
        Swal.fire("Booked!", "You have successfully booked.", "success");
      }
    });
  };

  useEffect(() => {
    setSelectedTime(null);
    setIsToday(false);
    if (
      date &&
      (new Date(date) > new Date() ||
        (date.getFullYear() === new Date().getFullYear() &&
          date.getMonth() === new Date().getMonth() &&
          date.getDate() === new Date().getDate()))
    ) {
      if (new Date(date) <= new Date()) {
        setIsToday(true);
      }
      setValidTime(doctor.availability[date.getDay()]);
    }
  }, [date]);

  return (
    <Layout title="Profile" page="profile">
      <div className="container mx-auto pt-20 px-4">
        <div className="flex gap-8 items-center sm:flex-row flex-col">
          <div className="flex items-center flex-col gap-2">
            <img src={doctor.image} alt={doctor.name} className="w-60 h-60" />
            <p className="text-slate-500">{doctor.type}</p>
          </div>
          <div className="space-y-3">
            <div className="flex gap-3 text-xl items-center">
              <p className="text-slate-500">Name:</p>
              <p className="text-2xl">{doctor.name}</p>
            </div>
            <div className="flex gap-3 text-xl">
              <p className="text-slate-500">Email:</p>
              <p>{doctor.email}</p>
            </div>
            <div className="flex gap-3 text-xl">
              <p className="text-slate-500">Phone:</p>
              <p>{doctor.phone}</p>
            </div>
            <div className="flex gap-3 text-xl">
              <p className="text-slate-500">Location:</p>
              <p>{doctor.location}</p>
            </div>
            <div className="flex gap-3 text-xl">
              <p className="text-slate-500">License Number:</p>
              <p>{doctor.license}</p>
            </div>
          </div>
        </div>
        <div className="flex flex-col">
          <p></p>
          <div className="flex md:flex-row flex-col gap-12 items-center">
            <Calendar cla onChange={(item) => setDate(item)} date={date} />
            {(date > new Date() || isToday) && (
              <div className="flex flex-col gap-12 justify-center w-full">
                <div className="gap-3 items-center md:flex grid sm:grid-cols-4 grid-cols-2 md:flex-wrap">
                  {validTime &&
                    validTime.time.map((time, idx) =>
                      isToday ? (
                        time > new Date().getHours() && (
                          <ItemTime
                            key={idx}
                            action={setSelectedTime}
                            selected={selectedTime}
                          >
                            {time}
                          </ItemTime>
                        )
                      ) : (
                        <ItemTime
                          key={idx}
                          action={setSelectedTime}
                          selected={selectedTime}
                        >
                          {time}
                        </ItemTime>
                      )
                    )}
                </div>
                <div>
                  <PrimaryButton
                    className="px-12 py-2 h-14"
                    active={selectedTime}
                    action={bookHandler}
                  >
                    <p className="text-xl">Book</p>
                  </PrimaryButton>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ProfilePage;
