import { useState } from "react";

export const PrimaryButton = (props) => {
  const { className, action, children, active } = props;
  const [hovered, setHovered] = useState(false)

  const actionHandler = () => {
    if(active && action){
      action()
    }
  }
  return (
    <div className={`${className} cursor-pointer relative overflow-hidden rounded-full  ${active?"bg-app-primary text-white":"bg-gray-200 text-gray-400"} `} onMouseEnter={()=>setHovered(true)} onMouseLeave={()=>setHovered(false)}>
      <div className={`bg-black bg-opacity-50 absolute left-0 top-0 h-full w-full transform duration-150 rounded-full ${active && hovered ? "translate-x-0 opacity-100" :"-translate-x-full opacity-0"}`}></div>
      <button
        className={`w-full  rounded-full   font-bold absolute left-1/2 top-1/2 transform -translate-x-1/2 -translate-y-1/2`}
        onClick={actionHandler}
      >
        {children}
      </button>
    </div>
  );
};
