import { useState } from "react";
import { Link } from "react-router-dom";

export const ItemDoctor = (props) => {
  const { doctor } = props;
  const [state, setState] = useState({
    doctor: doctor.id,
  });
  return (
    <div className="flex justify-between w-full py-3 sm:items-center items-start flex-col sm:flex-row">
      <div className="flex flex-1 items-center gap-4 ">
        <img src={doctor.image} alt={doctor.name} className="rounded-full" />
        <div>
          <p className="text-2xl text-gray-600">{doctor.name}</p>
          <p className="text-slate-400">{doctor.type}</p>
        </div>
      </div>
      <div className="items-end flex w-full sm:w-max justify-end sm:flex-col gap-2 md:items-center">
        <Link to={`/profile/${doctor.id}`}>
          <button className="px-8 py-2 border rounded-md hover:scale-105 active:scale-100 transform duration-150 hover:bg-app-primary hover:text-white hover:font-bold">
            View Profile
          </button>
        </Link>
        <Link to={`/profile/${doctor.id}`}>
          <button className="px-8 py-2 border rounded-md hover:scale-105 active:scale-100 transform duration-150 hover:bg-app-primary hover:text-white hover:font-bold">
            Book Online
          </button>
        </Link>
      </div>
    </div>
  );
};

export const ItemTime = (props) => {
  const { children, className, action, selected } = props;

  return (
    <button
      className={`${className}  rounded-md border font-bold hover:bg-green-300 hover:ring-1 hover:border-green-700 hover:ring-green-400 px-8 py-2 hover:text-slate-600 duration-150 active:scale-95 ${
        selected === children
          ? "border-green-700 ring-green-600 bg-green-300 text-slate-600"
          : "bg-slate-50"
      }`}
      onClick={() => action(children)}
    >
      {children > 12 ? `${children - 12}:00 PM` : `${children}:00 AM`}
    </button>
  );
};
