import React, { useEffect, useState } from "react";
import Select from "react-select";
import { ItemDoctor } from "../../components/item";
import Layout from "../../layout/layout";
import { doctors } from "../../utils/api";

const HomePage = (props) => {
  const doctorSpecialties = [
    {
      value: "all",
      label: "All",
    },
    {
      value: "General Practice",
      label: "General Practice",
    },
    {
      value: "Obstetrics & Gynaecology",
      label: "Obstetrics & Gynaecology",
    },
    {
      value: "Dermatology & Venereology",
      label: "Dermatology & Venereology",
    },
    {
      value: "Anaesthesiology",
      label: "Anaesthesiology",
    },
    {
      value: "Anatomical Pathology",
      label: "Anatomical Pathology",
    },
    {
      value: "Cardiology",
      label: "Cardiology",
    },
    {
      value: "Cardiothoracic Surgery",
      label: "Cardiothoracic Surgery",
    },
    {
      value: "Chinese Medicine",
      label: "Chinese Medicine",
    },
    {
      value: "Clinical Microbiology & Infection",
      label: "Clinical Microbiology & Infection",
    },
    {
      value: "Clinical Oncology",
      label: "Clinical Oncology",
    },
    {
      value: "Clinical Psychology",
      label: "Clinical Psychology",
    },
    {
      value: "Community Medicine",
      label: "Community Medicine",
    },
    {
      value: "Counselling Psychology",
      label: "Counselling Psychology",
    },
    {
      value: "Dentistry",
      label: "Dentistry",
    },
    {
      value: "Dietetics",
      label: "Dietetics",
    },
    {
      value: "Emergency Medicine",
      label: "Emergency Medicine",
    },
    {
      value: "Endocrinology - Diabetes & Metabolism",
      label: "Endocrinology - Diabetes & Metabolism",
    },
    {
      value: "Family Medicine",
      label: "Family Medicine",
    },
  ];
  const doctorGender = [
    {
      value: "male",
      label: "Male",
    },
    {
      value: "female",
      label: "Feale",
    },
    {
      value: "any",
      label: "Any",
    },
  ];
  const [filterSpec, setFilterSpec] = useState({
    value: "all",
    label: "All",
  });
  const [filterGender, setFilterGender] = useState({
    value: "any",
    label: "Any",
  });
  const [doctorList, setDoctorList] = useState(doctors);
  // const [filterName, setFilterName] = useState("")

  useEffect(() => {
    let buffer = [];
    buffer =
      filterSpec.value === "all"
        ? doctors
        : doctors.filter((e) => e.type === filterSpec.value);
    if (filterGender.value !== "any") {
      setDoctorList(buffer.filter((e) => e.gender === filterGender.value));
    } else {
      setDoctorList(buffer);
    }
  }, [filterSpec, filterGender]);
  return (
    <Layout>
      <div className="container mx-auto flex relative gap-12 pt-20  flex-col lg:flex-row px-2">
        <div className="w-full lg:w-80 xl:w-1/3 p-8 border rounded-xl h-max space-y-2 lg:space-y-8">
          <p className="text-xl">Doctor Filter</p>
          <div className="flex  lg:flex-col flex-col sm:flex-row gap-8">
            <div>
              <p className="py-2 text-slate-400">Specialties</p>
              <Select
                value={filterSpec}
                onChange={(option) => setFilterSpec(option)}
                options={doctorSpecialties}
              />
            </div>
            <div>
              <p className="py-2 text-slate-400">Doctor Gender</p>
              <Select
                value={filterGender}
                onChange={(option) => setFilterGender(option)}
                options={doctorGender}
              />
            </div>
          </div>
        </div>
        <ul className="divide-y w-full">
          {doctorList.map((doctor, idx) => (
            <ItemDoctor doctor={doctor} key={idx} />
          ))}
        </ul>
      </div>
    </Layout>
  );
};

export default HomePage;
