import { Link } from "react-router-dom";
import ImgLogo from "../assets/image/logo.png";
import { PrimaryButton } from "../components/button";
import { useAppContext } from "../contexts/AppContext";
const Navbar = () => {
  const context = useAppContext();
  return (
    <nav className="w-full">
      <div className="container mx-auto justify-between flex py-3 px-4 items-center">
        <Link to={"/"}>
          <img src={ImgLogo} alt="necktie" className="w-32" />
        </Link>
        <div>
          {context.user && (
            <Link to={"/login"}>
              <button className="text-xl underline cursor-pointer" onClick={()=>context.setUser({})}>
                {context.user.name}
              </button>
            </Link>
          )}
          {!context.user && (
            <Link to={"/login"}>
              <PrimaryButton active="true" className="w-28 h-8">
                Login
              </PrimaryButton>
            </Link>
          )}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
